import { NextResponse } from "next/server";
import { rapidFetch } from "../../_utils/rapidapi";

export async function GET(req: Request) {
  const { searchParams } = new URL(req.url);
  const hotel_id = searchParams.get("hotel_id");

  if (!hotel_id) {
    return NextResponse.json({ error: "Missing hotel_id" }, { status: 400 });
  }

  try {
    const data = await rapidFetch("/v1/hotels/reviews", {
      hotel_id,
      locale: "en-gb",
      page_number: "0",
      sort_type: "SORT_MOST_RELEVANT",
      customer_type: "solo_traveller,review_category_group_of_friends",
      language_filter: "en-gb,de,fr",
    });

    return NextResponse.json(data);
  } catch (err: any) {
    return NextResponse.json({ error: err.message }, { status: 500 });
  }
}
